<template>
  <div class='right_frame'>
    <h3>
      <span class='login_ico'></span<span>您的帐号近期没有异常登录</span>
    </h3>
    <div class='info'>
      <span>由于您的宽带运营商会不定期调整网络，我们获取的IP所在地可能不准确，请您通过登录时间与产品判断是否为您本人操作；如确定非您本人登录，建议您</span>
      <router-link :to='modfiyPasswordPath'>修改密码</router-link>。
    </div>
    <div>
     
    </div>
  </div>
</template>
<script>
import Paths from '@/config/path.js'

export default {
  data() {
    return {
      modfiyPasswordPath:Paths.userSecuritys.userPassword,
      user: null,
      util: this.$root
  
    };
  },
  created() {
    this.user = this.util.getUser();
    
  }
};
</script>
<style lang="scss" scoped>
@import "./default.scss";
.login_ico {
  height: 32px;
  margin-right: 6px;
  vertical-align: -8px;
  display: inline-block;
  width: 32px;
  background: url(../../../assets/uc-ico.png) no-repeat -128px 0;
}

.right_frame > h3 {
  font-size: 1.4em;
  font-weight: bold;
  color: #666;
}
.info {
  line-height: 1.5em;
}
</style>



